.jobs{

  &__skills{
    position: absolute;
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #E7E8EA;
    width: 400px;
    height: 450px;

    @include tablet{
      width: 298px;
    }

    @include tablet-pro{
      width: 255px;
    }

    @include tablet-pro{
      width: 260px;
    }

    @include small-duo{
      width: 164px;
      margin-top: 205px;
    }

    @include mobile{
      margin-top: 190px;
    }

    @include mobile-pro{
      width: 155px;
    }

    @include mobile-xr{
      width: 161px;
    }

    @include mobile-pro{
      margin-top: 150px;
    }

    @include mobile-12pro{
      margin-top: 198px;
      width: 155px;
    }

    @include mobile-min{
      width: 151px;
    }

    @include mobile-galaxys{
      width: 109px;
    }

    @include mobile-galaxys{
     padding: 0px;
    }
  }

  &__title{
    padding-top: 70px;
    padding-bottom: 20px;
    font-weight: 500;
    font-size: 40px;
    color: #161A25;
    background-color: #E7E8EA;
    width: 380px;


    @include tablet{
      width: 278px;
      font-size: 30px;
    }

    @include tablet-pro{
      width: 235px;
    }

    @include small-duo{
      width: 144px;
      padding-top: 0px;
    }

    @include mobile-xr{
      width: 140px;
    }

    @include mobile-pro{
      width: 131px;
    }

    @include mobile-min{
      width: 120px;
    }

    @include mobile-galaxys{
      width: 89px;
    }

  }

  &__center{
    display: flex;
    align-items: flex-start;

    @include tablet{
      display: flex;
      flex-direction: column;
    }
  }


  &__item{
    display: flex;
    align-items: center;
    text-align: center;
    width: 200px;
    padding-bottom: 10px;
  }

  &__text{
    font-size: 20px;
    color: #525252;

    @include tablet{
      font-size: 17px;
    }
  }

  &__abaut{
    padding-top: 70px;
    position: relative;
    left: 475px;
    width: 719px;

    @include tablet{
      left: 345px;
      width: 658px;
    }

    @include tablet-pro{
      left: 277px;
    }

    @include small-duo{
      left: 184px;
      width: 280px;
      padding-top: 205px;
    }

    @include mobile{
        padding-top: 190px;
      }

    @include mobile-pro{
      padding-top: 150px;
    }

    @include mobile-12pro{
        padding-top: 198px;
      }

    @include mobile-pixel{
      left: 173px;
    }

    @include mobile-min{
      left: 157px;
    }

    @include mobile-galaxys{
      left: 130px;
    }

    @include mobile-small{
      left: 109px;
      }


    &-top{
      padding-bottom: 20px;
      margin: 0;
      font-weight: 500;
      font-size: 40px;
      color: #161A25;

      @include tablet{
        font-size: 30px;
      }
    }

    &-text{
      font-size: 20px;
      color: #525252;
      margin: 0;
    }
  }
}

.link{
  @include mobile{
    font-size: 17px;
  }
}







.loader {
  height: 30px;
}

.loader > div {
  width: 15px;
  height: 15px;
  border-radius: 100%;
  float: left;
  margin: 5px;

  @include mobile-galaxys{
    margin: 2px;
  }
}

.loader > div:nth-child(1) {
  background: #171B26;
}



.jobs__text {
	opacity: 0;
	transform: translateY(50px);
	transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.jobs__text.animation-class {
	opacity: 1;
	transform: translateY(0);
	transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out;
}



.loader{
	opacity: 0;
	transform: translateY(50px);
	transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.loader.animation-class {
	opacity: 1;
	transform: translateY(0);
	transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out;
}



.jobs__abaut-text{
	opacity: 0;
	transform: translateY(50px);
	transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.jobs__abaut-text.animation-class {
	opacity: 1;
	transform: translateY(0);
	transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out;
}


.link{
	opacity: 0;
	transform: translateY(50px);
	transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.link.animation-class {
	opacity: 1;
	transform: translateY(0);
	transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out;
}
