.body{

  &__skills{
    position: absolute;
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #E7E8EA;
    width: 400px;
    height: 670px;

    @include tablet{
      width: 298px;
    }

    @include tablet-pro{
      width: 255px;
    }

    @include tablet-pro{
      width: 260px;
    }

    @include small-duo{
      width: 164px;
    }

    @include mobile-xr{
      width: 161px;
    }

    @include mobile-pro{
      width: 155px;
    }

    @include mobile-min{
      width: 151px;
    }

    @include mobile-galaxys{
      width: 109px;
      padding-right: 0px;
    }
  }

  &__title{
    padding-top: 70px;
    padding-bottom: 20px;
    font-weight: 500;
    font-size: 40px;
    color: #161A25;
    background-color: #E7E8EA;
    width: 380px;

    @include tablet{
      width: 278px;
      font-size: 30px;
    }

    @include tablet-pro{
      width: 235px;
    }

    @include small-duo{
      width: 144px;
    }

    @include mobile-xr{
      width: 141px;
    }

    @include mobile-pro{
      width: 135px;
    }

    @include mobile-min{
      width: 124px;
      padding-top: 30px;
    }

    @include mobile-galaxys{
      width: 87px;
    }
  }

  &__center{
    display: flex;
    align-items: flex-start;

    @include small-duo{
      display: flex;
      flex-direction: column-reverse;
    }
  }

  &__left{
    padding-right: 100px;


    @include tablet{
      padding-right: 50px;
    }


    @include small-duo{
      padding: 0;
    }
  }

  &__item{
    padding-bottom: 10px;
  }

  &__text{
    font-size: 20px;
    color: #525252;

    @include tablet{
      font-size: 17px;
    }
  }

  &__abaut{
    padding-top: 70px;
    position: relative;
    left: 475px;
    height: 430px;
    width: 719px;

    @include tablet{
      left: 345px;
      width: 658px;
    }

    @include tablet-pro{
      width: 554px;
    }

    @include tablet-pro{
      left: 277px;
      width: 450px;
    }

    @include small-tablet{
      width: 375px;
    }

    @include small-duo{
      left: 184px;
      width: 280px;
    }

    @include mobile{
      width: 230px;
    }

    @include mobile-pro{
      width: 220px;
    }

    @include mobile-pixel{
      left: 173px;
    }

    @include mobile-min{
      left: 157px;
      padding-top: 30px;
    }

    @include mobile-galaxys{
      left: 130px;
    }

    @include mobile-small{
      left: 109px;
      }

    &-top{
      padding-bottom: 20px;
      margin: 0;
      font-weight: 500;
      font-size: 40px;
      color: #161A25;

      @include tablet{
        font-size: 30px;
      }
    }

    &-text{
      font-size: 20px;
      line-height: 32px;
      color: #525252;
      margin: 0;

      @include mobile{
        font-size: 17px;
      }
    }
  }
}




.html {
  height: 30px;
}

.html > div {
  width: 15px;
  height: 15px;
  border-radius: 100%;
  float: left;
  margin: 5px;
}



.html > div:nth-child(1) {
  background: #171B26;
}

.html > div:nth-child(2) {
  background: #171B26;
}

.html > div:nth-child(3) {
  background: #171B26;
}

.html > div:nth-child(4) {
  background: #171B26;

  @include tablet-pro{
    display: none;
  }
}

.html > div:nth-child(5) {
  background: #171B26;

  @include tablet{
    display: none;
  }
}




