.header{

  background-color: #171B26;

  &__body{
    display: flex;

    @include mobile-galaxys{
     flex-direction: row-reverse;
    }
  }

  &__img{
    background-color: #212530;
    padding-top: 120px;
    width: 400px;
    height: 670px;

    @include tablet-ipad{
      height: 553px;
    }

    @include tablet-pro{
      padding-top: 50px;
      height: 470px;
    }

    @include mobile-galaxys{
      background-color: #171B26;
      height: 0;
    }
  }

  &__image{
    width: 400px;
    height: 450px;

    @include tablet-ipad{
      height: 335px;
    }

    @include small-duo{
      height: 260px;
    }

    @include mobile-min{
      height: 200px;
    }

    @include mobile-galaxys{
      width: 0;
      height: 0;
    }
  }

  &__info{
    display: flex;
    flex-direction: column;
    padding-left: 120px;
    padding-top: 120px;

    @include tablet-pro{
      padding-top: 50px;
    }

    @include small-tablet{
      padding-left: 36px;
    }

    @include mobile-min{
      padding-left: 20px;
    }

    @include mobile-galaxys{
      display: flex;
      flex-direction: row;
      padding-left: 5px;
    }

  }

  &__text{
    font-weight: 500;
    font-size: 70px;
    color: #FFFFFF;

    @include tablet{
      font-weight: 400;
      font-size: 50px;
    }

    @include tablet-ipad{
      font-size: 45px;
    }
  }

  &__profession{
    display: inline-block;
    padding: 5px 5px;
    font-size: 40px;
    background-color: #2E323B;
    color: #FFFFFF;

    @include tablet{
      font-size: 30px;
    }

    @include tablet-ipad{
      font-size: 25px;
    }

    @include mobile-galaxys{
     margin-right: 20px;
    }

    @include mobile-small{
    margin-right: 5px;
    padding: 2px 2px;
    }
  }

  &__title{
    font-weight: 500;
    font-size: 26px;
    color: #FFFFFF;

    @include tablet{
      margin: 0;
      padding-top: 10px;
      padding-bottom: 10px;
    }

    @include tablet-ipad{
      font-size: 20px;
    }

    @include small-duo{
      font-size: 17px;
      padding-top: 5px;
    }
  }

  &__bottom{

    font-weight: 300;
    font-size: 20px;
    color: #FFFFFF;

    &:hover{
      color: #726f6f;
    }

    @include tablet-ipad{
      font-size: 15px;
    }
  }

  &__contacts{
    display: grid;
    text-align:left;
    grid-template-columns: repeat(2, 1fr);
    gap: 28px;

    @include tablet{
      gap: 20px;
    }

    @include small-tablet{
      display: flex;
      flex-wrap: wrap;
    }
  }
}

.container{
  @include mobile{
    padding: 0;
  }
}

